export const PRODUCTS_VIEW_FRAGMENT = /* GraphQL */ `
  fragment PRODUCTS_VIEW_FRAGMENT on ProductView {
    __typename
    name
    sku
    queryType
    visibility
    images {
      url
    }
    urlKey
    ... on SimpleProductView {
      price {
        final {
          amount {
            currency
            value
          }
        }
      }
    }
    ... on ComplexProductView {
      priceRange {
        maximum {
          final {
            amount {
              currency
              value
            }
          }
        }
        minimum {
          final {
            amount {
              currency
              value
            }
          }
        }
      }
    }
  }
`;
