import { PRODUCTS_VIEW_FRAGMENT } from './ProductsViewFragment';

export const UNIT_FRAGMENT = /* GraphQL */ `
  fragment UNIT_FRAGMENT on RecommendationUnit {
    displayOrder
    pageType
    productsView {
      ...PRODUCTS_VIEW_FRAGMENT
    }
    storefrontLabel
    totalProducts
    typeId
    unitId
    unitName
  }

  ${PRODUCTS_VIEW_FRAGMENT}
`;
